.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
  margin: 0 auto;
  background: #fc466b;
  background: linear-gradient(to right, #fc466b, #3f5efb);
}

.Note {
  word-wrap: break-word;
}

.Creator {
  padding: 1rem;
}

.Board {
  margin: 1rem auto;
  width: 100%;
  height: 50vh;
  padding: 1rem;
}

.NotesList {
  margin-top: 2rem;
  padding: 1rem;
  height: 100%;
  overflow: scroll;
  overflow-x: hidden;
}

.Notification {
  margin-bottom: 0.6rem;
  padding: 0.5rem;
  width: auto;
  border-bottom: 1px dotted #fc466b;
}
.Author {
  color: #fc466b;
}

.Wall {
  background-color: #ffffff;
  width: 80%;
  margin: 0 auto;
  padding: 1rem;
  border-radius: 1rem;
  -webkit-justify-content: center;
          justify-content: center;
}
.WallNav {
  padding: 1rem;
}
.Wrapped {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.Notifications {
  margin: 0 auto;
  width: 20%;
}

.Home {
  height: 100vh;
  width: 80%;
  margin: 0 auto;
}

.HomeInfo {
  padding: 2rem;
  text-align: center;
  color: #fff;
}

.Sign {
  background-color: #ffffff;
  margin: 0 auto;
  padding: 1rem;
  border-radius: 1rem;
  width: 40%;
  height: auto;
}

.Error {
  margin: 0 auto;
  text-align: center;
  color: rgb(243, 51, 51);
  font-weight: bold;
  margin-top: 2rem;
}

html {}

body {
  font-size: 62.5%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

